.SecondScroll {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; }
  .SecondScroll .title {
    width: 80%;
    height: 5%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center; }
    .SecondScroll .title div {
      grid-column: 2;
      text-align: center;
      font-size: 2rem; }
    .SecondScroll .title svg {
      padding-left: 85%; }
  .SecondScroll .projects {
    padding: 0 10%;
    width: 100%;
    height: 85%;
    display: grid;
    grid-column-gap: 40px;
    grid-row-gap: 40px; }
    .SecondScroll .projects .project {
      border: 2px solid burlywood;
      background-color: orangered;
      box-shadow: 0px 0px 13px 5px rgba(224, 224, 224, 0.75); }
  .SecondScroll .oneThird {
    grid-template-columns: repeat(3, 1fr); }
  .SecondScroll .oneFourth {
    grid-template-columns: repeat(4, 1fr); }
  .SecondScroll .half {
    grid-template-columns: repeat(2, 1fr); }
  .SecondScroll .fullWidth {
    grid-template-rows: repeat(1fr, 2); }
