.firstScroll {
  background-image: linear-gradient(180deg, darkgray, cornflowerblue);
  color: beige;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center; }
  .firstScroll .intro {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
    .firstScroll .intro .circle {
      height: 300px;
      width: 300px;
      border: 5px whitesmoke solid;
      border-radius: 50%;
      background-image: url("https://avatars0.githubusercontent.com/u/11410367?s=400&u=d6c3e64d74f74070a8a0a717a0497d4a177ca110&v=4");
      background-position: center;
      background-size: cover;
      filter: grayscale(75%); }
    .firstScroll .intro .name,
    .firstScroll .intro .job,
    .firstScroll .intro .place {
      text-align: center;
      color: cornsilk; }
    .firstScroll .intro .name {
      font-weight: bolder;
      font-size: 3em; }
    .firstScroll .intro .job {
      font-weight: bold;
      font-size: 20px; }
    .firstScroll .intro .place {
      font-weight: 500;
      font-size: 20px;
      line-height: 20px; }
  .firstScroll .social-media {
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    align-items: center; }
